import { observer } from "mobx-react-lite";
import { useEffect, useState } from "react";
import { Link, useHistory, useParams } from "react-router-dom";
import { Button, Header, Segment } from "semantic-ui-react";
import LoadingComponent from "../../../app/layout/LoadingComponent";
import { useStore } from "../../../app/stores/store";
import { v4 as uuid } from "uuid";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MyTextArea from "../../../app/common/form/MyTextArea";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { categoryOptions } from "../../../app/common/options/categoryOptions";
import MyDateInput from "../../../app/common/form/MyDateInput";
import { ActivityFormValue } from "../../../app/models/activity";

const ActivityForm = () => {
    const history = useHistory();
    const { activityStore } = useStore();
    const { createActivity, updateActivity, loadActivity, loadingInitial } =
        activityStore;
    const { id } = useParams<{ id: string }>();
    const [activity, setActivity] = useState<ActivityFormValue>(
        new ActivityFormValue()
    );

    const validationSchema = Yup.object({
        title: Yup.string().required("The activity title is required"),
        description: Yup.string().required(
            "The activity description is required"
        ),
        category: Yup.string().required("The activity category is required"),
        date: Yup.string().required("The activity date is required").nullable(),
        city: Yup.string().required("The activity description is required"),
        venue: Yup.string().required("The activity venue is required"),
    });

    const handlerFormSubmit = (activity: ActivityFormValue) => {
        if (!activity.id) {
            let newActivity = {
                ...activity,
                id: uuid(),
            };
            createActivity(newActivity).then(() => {
                history.push(`/activities/${newActivity.id}`);
            });
        } else {
            updateActivity(activity).then(() =>
                history.push(`/activities/${activity.id}`)
            );
        }
    };

    useEffect(() => {
        if (id) {
            loadActivity(id).then((activity) =>
                setActivity(new ActivityFormValue(activity))
            );
        }
    }, [id, loadActivity]);

    if (loadingInitial)
        return <LoadingComponent content="Loading activity..." />;

    return (
        <Segment clearing>
            <Header content="Activity Details" sub color="teal" />
            <Formik
                validationSchema={validationSchema}
                initialValues={activity}
                enableReinitialize
                onSubmit={(values) => handlerFormSubmit(values)}
            >
                {({ handleSubmit, isValid, isSubmitting, dirty }) => (
                    <Form
                        className="ui form"
                        onSubmit={handleSubmit}
                        autoComplete="off"
                    >
                        <MyTextInput placeholder="Title" name="title" />
                        <MyTextArea
                            rows={3}
                            placeholder="Description"
                            name="description"
                        />
                        <MySelectInput
                            options={categoryOptions}
                            placeholder="Category"
                            name="category"
                        />
                        <MyDateInput
                            placeholderText="Date"
                            name="date"
                            showTimeSelect
                            timeCaption="time"
                            dateFormat="MMMM d, yyyy h:mm aa"
                        />
                        <Header content="Location Details" sub color="teal" />
                        <MyTextInput placeholder="City" name="city" />
                        <MyTextInput placeholder="Venue" name="venue" />
                        <Button
                            disabled={isSubmitting || !dirty || !isValid}
                            loading={isSubmitting}
                            floated="right"
                            positive
                            type="submit"
                            content="Submit"
                        />
                        <Button
                            as={Link}
                            to="/activities"
                            floated="right"
                            type="button"
                            content="Cancel"
                        />
                    </Form>
                )}
            </Formik>
        </Segment>
    );
};

export default observer(ActivityForm);
